.App {
  text-align: end;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}



.navbar-collapse{
  align-items: flex-end;
  flex-direction:column;
}
a {
  margin: 10px;
  float:right;
}
button {
  margin: 10px;
}



@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
